.about-story {
  padding: 50px;
  h3 {
    font-family: 'Kaushan Script', cursive;
  }
  h2 {
    font-size: 30px;
    font-weight: 700;
    padding: 10px;
  }
  .border-bottom-about {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px 30px 30px 30px;
    .border-about {
      background-color: #f38181;
      width: 60px;
      height: 2px;
    }
  }
  p {
    color: #999999;
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
  }
}

.about-imgs {
  margin-bottom: 50px;
  img {
    width: 100%;
    padding: 10px;
  }
}